import React from "react";
// import { UncontrolledTooltip } from "reactstrap";

//components
// import AddButton from "components/AddButton";

// component
import ChatUser from "./ChatUser";

const DirectMessages = ({
  token,
  directMessages,
  openAddContact,
  selectedChat,
  onSelectChat,
  user,
  setMessages,
}) => {
  return (
    <>
      <div className="d-flex align-items-center px-4 mt-5 mb-2">
        <div className="flex-grow-1">
          <h4 className="mb-0 font-size-11 text-muted text-uppercase">
            Direct Messages
          </h4>
        </div>
        {/* <div className="flex-shrink-0">
          <div id="new-message" title="New Message">
            Button trigger modal
            <AddButton icon="plus" onClick={openAddContact} />{" "}
            contactModal
          </div>
          <UncontrolledTooltip target="new-message" placement="bottom">
            New Message
          </UncontrolledTooltip>
        </div> */}
      </div>

      <div className="chat-message-list">
        <ul id="chat-list" className="list-unstyled chat-list chat-user-list">
          {(directMessages || []).map((directContacts, key) => (
            <ChatUser
              directContacts={directContacts}
              token={token}
              key={key}
              onSelectChat={onSelectChat}
              selectedChat={selectedChat}
              user={user}
              setMessages={setMessages}
            />
          ))}
        </ul>
      </div>
    </>
  );
};

export default DirectMessages;
