import React, { useEffect, useRef, useState } from "react";
import classnames from "classnames";

// hooks
// import { useRedux } from "hooks/index";

// hooks
import { useConversationUserType } from "hooks/index";

// component
import Leftbar from "./Leftbar";
import ConversationUser from "./ConversationUser/index";
import UserProfileDetails from "./UserProfileDetails/index";
import Welcome from "./ConversationUser/Welcome";
import { compose } from "redux";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import Loader from "components/Loader";
import ChatActions from "redux/chats_new/action";
import config from "config/vars";
import { io } from "socket.io-client";

const { fetching } = ChatActions;

const Index = (props) => {
  const { SOCKET_URL } = config;
  let socket = useRef();

  const { token, isUserLogin, history, user, selectedChat, isFetching } = props;

  let { messages } = props;

  const [allMessages, setAllMessages] = useState([]);
  const [activeUsers, setActiveUsers] = useState([]);
  const [newMessages, setNewMessages] = useState(null);

  const { isChannel } = useConversationUserType();

  useEffect(() => {
    if (token && isUserLogin) {
      setTimeout(() => {
        history.push("/dashboard");
      }, 500);
    } else {
      history.push("/login");
    }
    // eslint-disable-next-line
  }, [token, isUserLogin]);

  useEffect(() => {
    socket.current = io.connect(SOCKET_URL);

    // eslint-disable-next-line
  }, [newMessages, user?._id]);

  useEffect(() => {
    setAllMessages(messages);
    // eslint-disable-next-line
  }, [messages?.length]);

  useEffect(() => {
    socket?.current?.emit("addUser", user._id);
    socket?.current?.on("getUsers", (users) => {
      setActiveUsers(users);
    });
    // eslint-disable-next-line
  }, [user?._id]);

  let id = user?._id;
  useEffect(() => {
    socket?.current?.on("getMessage", (data) => {
      if (data.receiverId === id) {
        setNewMessages({ ...data, createdAt: new Date() });
      }
    });
    // eslint-disable-next-line
  }, [user?._id]);

  return isFetching ? (
    <Loader />
  ) : (
    <>
      <Leftbar />
      {selectedChat ? (
        <div
          className={classnames(
            "user-chat",
            "w-100",
            "overflow-hidden",
            {
              "user-chat-show": selectedChat,
            },
            [user?.themeBackground]
          )}
          id="user-chat"
        >
          <div className="user-chat-overlay" id="user-chat-overlay"></div>
          <div className="chat-content d-lg-flex">
            <div className="w-100 overflow-hidden position-relative">
              <ConversationUser
                allMessages={allMessages}
                setAllMessages={setAllMessages}
                isChannel={isChannel}
                user={user}
                socket={socket}
                activeUsers={activeUsers}
                setActiveUsers={setActiveUsers}
                setNewMessages={setNewMessages}
                newMessages={newMessages}
              />
            </div>
            <UserProfileDetails isChannel={isChannel} />
          </div>
        </div>
      ) : (
        <Welcome />
      )}
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.Login?.user_token,
    isFetching: state.Navigation?.isFetching,
    isUserLogin: state.Login.isUserLogin,
    user: state.Navigation.user,
    selectedChat: state.ChatsNew.selectedChat,
    messages: state.Messages.messages,
  };
};

export default compose(
  withRouter,
  connect(mapStateToProps, { fetching })
)(Index);
