import React, { useEffect, useState } from "react";

// hooks
import { useRedux } from "hooks/index";

// components
// import Loader from "components/Loader";
import AppSimpleBar from "components/AppSimpleBar";
import InviteContactModal from "components/InviteContactModal";
import EmptyStateResult from "components/EmptyStateResult";
import ListHeader from "./ListHeader";
import Contact from "./Contact";

// actions
import {
  getContacts,
  inviteContact,
  resetContacts,
  // getChannelDetails,
  // getChatUserDetails,
  getChatUserConversations,
  changeSelectedChat,
} from "redux/actions";

//utils
// import { divideByKey } from "utils";
import { compose } from "redux";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import ChatAction from "redux/chats_new/action";
import { getContactsData } from "api/contacts_new";
import MessageLoader from "components/MessageLoader";

const { contactsData, selectedChatData } = ChatAction;

const Index = (props) => {
  const { contactsNew, contactsData, token, selectedChatData } = props;
  // global store
  const { dispatch, useAppSelector } = useRedux();

  const { contactsList, getContactsLoading, isContactInvited } = useAppSelector(
    (state) => ({
      contactsList: state.Contacts.contacts,
      getContactsLoading: state.Contacts.getContactsLoading,
      isContactInvited: state.Contacts.isContactInvited,
    })
  );

  /*
  get contacts
  */
  useEffect(() => {
    dispatch(getContacts());
  }, [dispatch]);

  const [contacts, setContacts] = useState([]);
  // const [contactsData, setContactsData] = useState([]);
  useEffect(() => {
    if (contactsList.length > 0) {
      setContacts(contactsList);
    }
  }, [contactsList]);

  // useEffect(() => {
  //   if (contacts.length > 0) {
  //     const formattedContacts = divideByKey("firstName", contacts);
  //     setContactsData(formattedContacts);
  //   }
  // }, [contacts]);

  /*
  add contact modal handeling
  */
  const [isOpen, setIsOpen] = useState(false);
  const openModal = () => {
    setIsOpen(true);
  };
  const closeModal = () => {
    setIsOpen(false);
  };

  /*
  onInvite handeling
  */
  const onInviteContact = (data) => {
    dispatch(inviteContact(data));
  };
  useEffect(() => {
    if (isContactInvited) {
      setIsOpen(false);
      setTimeout(() => {
        dispatch(resetContacts("isContactInvited", false));
      }, 1000);
    }
  }, [dispatch, isContactInvited]);

  /*
  contact search
  */
  const [search, setSearch] = useState("");
  const onChangeSearch = (value) => {
    setSearch(value);
    let modifiedContacts = [...contactsList];
    let filteredContacts = (modifiedContacts || []).filter((c) =>
      c["firstName"].toLowerCase().includes(value.toLowerCase())
    );
    setContacts(filteredContacts);
  };

  const totalC = (contacts || []).length;
  const onSelectChat = (id, isChannel) => {
    // if (isChannel) {
    //   dispatch(getChannelDetails(id));
    // } else {
    //   dispatch(getChatUserDetails(id));
    // }
    selectedChatData(id);
    dispatch(getChatUserConversations(id));
    dispatch(changeSelectedChat(id));
  };

  const loadContacts = () => {
    getContactsData(token).then((res) => {
      if (res.success) {
        contactsData(res.data);
      }
    });
  };

  useEffect(() => {
    loadContacts();
    // eslint-disable-next-line
  }, []);

  return (
    <>
      <div className="position-relative">
        {getContactsLoading && <MessageLoader />}
        <ListHeader
          search={search}
          onChangeSearch={onChangeSearch}
          openModal={openModal}
        />

        <AppSimpleBar className="chat-message-list chat-group-list">
          <div>
            {totalC === 0 ? (
              <EmptyStateResult searchedText={search} />
            ) : (
              <Contact contactsNew={contactsNew} onSelectChat={onSelectChat} />
            )}
          </div>
        </AppSimpleBar>
      </div>
      <InviteContactModal
        isOpen={isOpen}
        onClose={closeModal}
        onInvite={onInviteContact}
      />
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.Login.user_token,
    contactsNew: state.ChatsNew?.contacts,
  };
};

export default compose(
  withRouter,
  connect(mapStateToProps, { contactsData, selectedChatData })
)(Index);
