import React, { useState } from "react";
import { compose } from "redux";

// hooks
import { useRedux } from "hooks/index";

// actions
import {
  toggleUserDetailsTab,
  // getChatUserConversations,
  // onSendMessage,
  // receiveMessage,
  // readMessage,
  // receiveMessageFromUser,
  // deleteMessage,
  // deleteUserMessages,
  // toggleArchiveContact,
} from "redux/actions";

// components
import UserHead from "./UserHead";
import Conversation from "./Conversation";
import ChatInputSection from "./ChatInputSection/index";

// dummy data
import { pinnedTabs } from "data/index";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import NavigationActions from "redux/navigation/actions";
import { storeMessages } from "api/messages";

const { success, error } = NavigationActions;

const Index = (props) => {
  // global store

  let {
    isChannel,
    selectedChat,
    user,
    setAllMessages,
    allMessages,
    token,
    socket,
    newMessages,
    setNewMessages,
  } = props;

  const { dispatch } = useRedux();
  // const {
  //   // chatUserDetails,
  //   // chatUserConversations,
  //   // isUserMessageSent,
  //   // isMessageDeleted,
  //   // isMessageForwarded,
  //   // isUserMessagesDeleted,
  //   // isImageDeleted,
  // } = useAppSelector((state) => ({
  //   // chatUserDetails: state.Chats.chatUserDetails,
  //   // chatUserConversations: state.Chats.chatUserConversations,
  //   // isUserMessageSent: state.Chats.isUserMessageSent,
  //   // isMessageDeleted: state.Chats.isMessageDeleted,
  //   // isMessageForwarded: state.Chats.isMessageForwarded,
  //   // isUserMessagesDeleted: state.Chats.isUserMessagesDeleted,
  //   // isImageDeleted: state.Chats.isImageDeleted,
  // }));

  const onOpenUserDetails = () => {
    dispatch(toggleUserDetailsTab(true));
  };

  /*
  reply handeling
  */
  const [replyData, setReplyData] = useState(null);
  const onSetReplyData = (reply) => {
    setReplyData(reply);
  };

  /* send message */
  const onSend = (data) => {
    socket?.current?.emit("sendMessage", data);
    storeMessages(token, data);
    setNewMessages({ ...data, createdAt: new Date() });
  };

  // useEffect(() => {
  //   if (
  //     isUserMessageSent ||
  //     isMessageDeleted ||
  //     isMessageForwarded ||
  //     isUserMessagesDeleted ||
  //     isImageDeleted
  //   ) {
  //     dispatch(getChatUserConversations(chatUserDetails.id));
  //   }
  // }, [
  //   dispatch,
  //   isUserMessageSent,
  //   chatUserDetails,
  //   isMessageDeleted,
  //   isMessageForwarded,
  //   isUserMessagesDeleted,
  //   isImageDeleted,
  // ]);

  // const onDeleteMessage = (messageId) => {
  //   dispatch(deleteMessage(chatUserDetails.id, messageId));
  // };

  // const onDeleteUserMessages = () => {
  //   dispatch(deleteUserMessages(chatUserDetails.id));
  // };

  // const onToggleArchive = () => {
  //   dispatch(toggleArchiveContact(chatUserDetails.id));
  // };

  return (
    <>
      <UserHead
        pinnedTabs={pinnedTabs}
        onOpenUserDetails={onOpenUserDetails}
        // onDelete={onDeleteUserMessages}
        isChannel={isChannel}
        // onToggleArchive={onToggleArchive}
        selectedChat={selectedChat}
      />
      <Conversation
        selectedChat={selectedChat}
        // onDelete={onDeleteMessage}
        onSetReplyData={onSetReplyData}
        isChannel={isChannel}
        allMessages={allMessages}
        setAllMessages={setAllMessages}
        user={user}
        newMessages={newMessages}
      />
      <ChatInputSection
        onSend={onSend}
        replyData={replyData}
        onSetReplyData={onSetReplyData}
        selectedChat={selectedChat}
        user={user}
      />
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.Login?.user_token,
    isFetching: state.Navigation?.isFetching,
    isUserLogin: state.Login.isUserLogin,
    selectedChat: state.ChatsNew.selectedChat,
    directMessages: state.ChatsNew.directMessages,
  };
};

export default compose(
  withRouter,
  connect(mapStateToProps, { success, error })
)(Index);
