import { api, handleResponse, handleError } from "services/ApiServices";

export const getMessagesByConversation = (token, conversationId) =>
  api(token)
    .get(`/messages/read/${conversationId}`)
    .then(handleResponse)
    .catch(handleError);

export const storeMessages = (token, data) =>
  api(token)
    .post(`/messages/create`, data)
    .then(handleResponse)
    .catch(handleError);
