import React, { useEffect } from "react";
import { Switch, Route, withRouter } from "react-router-dom";

// layouts
import NonAuthLayout from "layouts/NonAuth/index";
import { AuthProtected, AccessRoute } from "./AuthProtected";

import { publicRoutes, privateRoutes } from "./allRoutes";
import { ToastContainer } from "react-toastify";
import NavigationAction from "redux/navigation/actions";
import SettingsAction from "redux/settings_new/actions";

import { connect } from "react-redux";
import { compose } from "redux";
import { getPrivacySettings } from "api/settings";

let { check, fetching } = NavigationAction;
let { privacy } = SettingsAction;

const Index = (props) => {
  const { token, check, privacy, fetching, history } = props;

  const getUserDetails = () => {
    if (history.location.pathname === "/dashboard") {
      fetching(true);
    }
    check(token);
    getPrivacySettings(token).then((res) => {
      setTimeout(() => {
        fetching(false);
      }, 400);
      privacy(res.data);
    });
  };

  const availablePublicRoutesPaths = publicRoutes.map((r) => r.path);
  const availableAuthRoutesPath = privateRoutes.map((r) => r.path);

  useEffect(() => {
    getUserDetails();

    // eslint-disable-next-line
  }, [token]);

  return (
    <React.Fragment>
      <Switch>
        <Route path={availablePublicRoutesPaths}>
          <NonAuthLayout>
            <Switch>
              {publicRoutes.map((route, idx) => (
                <Route
                  path={route.path}
                  component={route.component}
                  key={idx}
                  exact={true}
                  history={history}
                />
              ))}
            </Switch>
          </NonAuthLayout>
        </Route>

        <Route path={availableAuthRoutesPath}>
          <AuthProtected>
            <Switch>
              {privateRoutes.map((route, idx) => (
                <AccessRoute
                  path={route.path}
                  component={route.component}
                  key={idx}
                  exact={true}
                  history={history}
                />
              ))}
            </Switch>
          </AuthProtected>
        </Route>
      </Switch>
      <ToastContainer />;
    </React.Fragment>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state?.Login?.user_token,
    isFetching: state.Navigation?.isFetching,
    isUserLogin: state.Login.isUserLogin,
  };
};

export default compose(
  withRouter,
  connect(mapStateToProps, { check, privacy, fetching })
)(Index);
