import React from "react";
import classnames from "classnames";
import { STATUS_TYPES } from "constants/settings";

const Status = ({ status }) => {
  return (
    <div className="text-muted pt-4">
      <h5 className="font-size-11 text-uppercase">Status :</h5>
      <p
        className={classnames(
          "mb-4",
          {
            "text-success": status === STATUS_TYPES.ACTIVE,
          },
          {
            "text-warning": status === STATUS_TYPES.AWAY,
          },
          {
            "text-danger": status === STATUS_TYPES.DO_NOT_DISTURB,
          }
        )}
      >
        {status ? status.charAt(0).toUpperCase() + status.slice(1) : "-"}
      </p>
    </div>
  );
};

export default Status;
