import React, { useEffect, useState } from "react";

import { Button, Form, UncontrolledTooltip } from "reactstrap";
import { Link, withRouter } from "react-router-dom";
// hooks
import { useRedux } from "hooks/index";
// actions
import {
  inviteContact,
  resetContacts,
  getFavourites,
  // getDirectMessages,
  getChannels,
  addContacts,
  createChannel,
  // changeSelectedChat,
  // getChatUserDetails,
  // getChatUserConversations,
  // getChannelDetails,
  // getArchiveContact,
  // readConversation,
} from "redux/actions";

// components
import AppSimpleBar from "components/AppSimpleBar";
import AddGroupModal from "components/AddGroupModal";
import InviteContactModal from "components/InviteContactModal";
import AddButton from "components/AddButton";
import ContactModal from "components/ContactModal";

// import Favourites from "./Favourites";
import DirectMessages from "./DirectMessages";
// import Chanels from "./Chanels";
import Archive from "./Archive";
import { CHATS_TABS } from "constants/layout";
import NavigationAction from "redux/navigation/actions";
import { connect } from "react-redux";
import { compose } from "redux";
import { getDirectMessages } from "api/chats_new";
import { getMessagesByConversation } from "api/messages";
import ChatAction from "redux/chats_new/action";
import MessageActions from "redux/messages/action";

let { success, error, loadingData } = NavigationAction;
const { directMessagesData, selectedChatData } = ChatAction;
const { setMessages } = MessageActions;

const Index = (props) => {
  const {
    token,
    success,
    error,
    directMessagesData,
    selectedChatData,
    selectedChat,
    user,
    directMessages,
    setMessages,
    loadingData,
  } = props;

  // global store
  const { dispatch, useAppSelector } = useRedux();

  const {
    isContactInvited,
    // favourites,
    // channels,
    isContactsAdded,
    isChannelCreated,
    isFavouriteContactToggled,
    archiveContacts,
    // isContactArchiveToggled,
    // chatUserDetails,
  } = useAppSelector((state) => ({
    isContactInvited: state.Contacts.isContactInvited,
    favourites: state.Chats.favourites,
    // channels: state.Chats.channels,
    isContactsAdded: state.Chats.isContactsAdded,
    isChannelCreated: state.Chats.isChannelCreated,
    isFavouriteContactToggled: state.Chats.isFavouriteContactToggled,
    archiveContacts: state.Chats.archiveContacts,
    isContactArchiveToggled: state.Chats.isContactArchiveToggled,
    chatUserDetails: state.Chats.chatUserDetails,
  }));

  // get data
  useEffect(() => {
    dispatch(getFavourites());
    dispatch(getChannels());
  }, [dispatch]);
  useEffect(() => {
    if (isFavouriteContactToggled) {
      dispatch(getFavourites());
    }
  }, [dispatch, isFavouriteContactToggled]);

  // invite contact modal handeling
  const [isOpen, setIsOpen] = useState(false);
  const openModal = () => {
    setIsOpen(true);
  };
  const closeModal = () => {
    setIsOpen(false);
  };

  // onInvite handeling
  const onInviteContact = (data) => {
    dispatch(inviteContact(data));
  };

  useEffect(() => {
    if (isContactInvited) {
      setIsOpen(false);
      setTimeout(() => {
        dispatch(resetContacts("isContactInvited", false));
      }, 1000);
    }
  }, [dispatch, isContactInvited]);

  // contact add handeling
  const [isOpenAddContact, setIsOpenAddContact] = useState(false);
  const openAddContactModal = () => {
    setIsOpenAddContact(true);
  };
  const closeAddContactModal = () => {
    setIsOpenAddContact(false);
  };
  const onAddContact = (contacts) => {
    dispatch(addContacts(contacts));
  };
  useEffect(() => {
    if (isContactsAdded) {
      setIsOpenAddContact(false);
    }
  }, [dispatch, isContactsAdded]);

  // channel creation handeling
  const [isOpenCreateChannel, setIsOpenCreateChannel] = useState(false);
  // const openCreateChannelModal = () => {
  //   setIsOpenCreateChannel(true);
  // };
  const closeCreateChannelModal = () => {
    setIsOpenCreateChannel(false);
  };
  const onCreateChannel = (channelData) => {
    dispatch(createChannel(channelData));
  };
  useEffect(() => {
    if (isChannelCreated) {
      setIsOpenCreateChannel(false);
      dispatch(getChannels());
    }
  }, [dispatch, isChannelCreated]);

  /* 
    select chat handeling :
    get conversations
    get chat user details
  */

  const onSelectChat = (conversation) => {
    setMessages("");
    selectedChatData(conversation);
    getMessages(conversation._id);
  };

  // tab handeling
  const [active, setActive] = useState(CHATS_TABS.DEFAULT);
  const onChangeTab = (tab) => {
    setActive(tab);
  };

  const getDirectMessagesFun = () => {
    getDirectMessages(token).then((res) => {
      if (res.success) {
        directMessagesData(res.data);
      }
    });
  };

  /* archive contacts */
  // useEffect(() => {
  //   dispatch(getArchiveContact());
  // }, [dispatch]);
  // useEffect(() => {
  //   if (isContactArchiveToggled) {
  //     dispatch(getArchiveContact());
  //     dispatch(getFavourites());
  //     dispatch(getChannels());
  //     dispatch(getChatUserDetails(chatUserDetails.id));
  //   }
  // }, [dispatch, isContactArchiveToggled, chatUserDetails.id]);

  const getMessages = (chat_id) => {
    loadingData(true);
    getMessagesByConversation(token, chat_id).then((res) => {
      if (res.success) {
        loadingData(false);
        setMessages(res.data);
      } else {
        loadingData(false);
        setMessages("");
      }
    });
  };

  useEffect(() => {
    getDirectMessagesFun();
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (selectedChat?._id) {
      getMessages(selectedChat?._id);
    }
    // eslint-disable-next-line
  }, []);

  return (
    <>
      <div>
        <div className="px-4 pt-4">
          <div className="d-flex align-items-start">
            <div className="flex-grow-1">
              <h4 className="mb-4">Chats</h4>
            </div>
            <div className="flex-shrink-0">
              <div id="add-contact">
                {/* Button trigger modal */}
                <AddButton icon="search" onClick={openModal} />
              </div>
              <UncontrolledTooltip target="add-contact" placement="bottom">
                Add Contact
              </UncontrolledTooltip>
            </div>
          </div>
          <Form>
            <div className="input-group mb-3">
              <input
                type="text"
                className="form-control bg-light border-0 pe-0"
                placeholder="Search here.."
                aria-label="Example text with button addon"
                aria-describedby="searchbtn-addon"
              />
              <Button
                className="btn btn-light"
                type="button"
                id="searchbtn-addon"
              >
                <i className="bx bx-search align-middle"></i>
              </Button>
            </div>
          </Form>
        </div>{" "}
        {/* .p-4 */}
        <AppSimpleBar className="chat-room-list">
          {/* Start chat-message-list */}
          {active === CHATS_TABS.DEFAULT && (
            <>
              {/* favourite */}
              {/* <Favourites
                users={favourites}
                selectedChat={selectedChat}
                onSelectChat={onSelectChat}
              /> */}

              {/* direct messages */}
              {directMessages && directMessages?.length > 0 && (
                <DirectMessages
                  token={token}
                  directMessages={directMessages}
                  openAddContact={openAddContactModal}
                  selectedChat={selectedChat}
                  onSelectChat={onSelectChat}
                  user={user}
                  setMessages={setMessages}
                />
              )}

              {/* channels list */}
              {/* <Chanels
                channels={channels}
                openCreateChannel={openCreateChannelModal}
                selectedChat={selectedChat}
                onSelectChat={onSelectChat}
              /> */}
              <h5 className="text-center mb-2">
                <Link
                  to="#"
                  className="mb-3 px-4 mt-4 font-size-11 text-primary"
                  onClick={() => onChangeTab(CHATS_TABS.ARCHIVE)}
                >
                  Archived Contacts{" "}
                  <i className="bx bxs-archive-in align-middle" />
                </Link>
              </h5>
            </>
          )}
          {active === CHATS_TABS.ARCHIVE && (
            <>
              <Archive
                archiveContacts={archiveContacts}
                selectedChat={selectedChat}
                onSelectChat={onSelectChat}
              />
              <h5 className="text-center mb-2">
                <Link
                  to="#"
                  className="mb-3 px-4 mt-4 font-size-11 text-primary"
                  onClick={() => onChangeTab(CHATS_TABS.DEFAULT)}
                >
                  Chats <i className="bx bxs-archive-out align-middle" />
                </Link>
              </h5>
            </>
          )}

          {/* End chat-message-list */}
        </AppSimpleBar>
      </div>
      {/* add group Modal */}
      {isOpenCreateChannel && (
        <AddGroupModal
          isOpen={isOpenCreateChannel}
          onClose={closeCreateChannelModal}
          onCreateChannel={onCreateChannel}
        />
      )}

      {/* add contact modal */}
      {isOpen && (
        <InviteContactModal
          isOpen={isOpen}
          onClose={closeModal}
          onInvite={onInviteContact}
          token={token}
          success={success}
          error={error}
        />
      )}

      {isOpenAddContact && (
        <ContactModal
          isOpen={isOpenAddContact}
          onClose={closeAddContactModal}
          onAddContact={onAddContact}
        />
      )}
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.Login?.user_token,
    loading: state.Navigation?.loading,
    isUserLogin: state.Login?.isUserLogin,
    directMessages: state.ChatsNew?.directMessages,
    selectedChat: state.ChatsNew?.selectedChat,
    user: state.Navigation?.user,
  };
};

export default compose(
  withRouter,
  connect(mapStateToProps, {
    directMessagesData,
    selectedChatData,
    success,
    error,
    setMessages,
    loadingData,
  })
)(Index);
