/* eslint-disable array-callback-return */
import React from "react";
import { Link } from "react-router-dom";
import classnames from "classnames";

import { STATUS_TYPES } from "constants/settings";
import config from "config/vars";
import { updateLastConversationId } from "api/user";

const ChatUser = ({
  token,
  directContacts,
  onSelectChat,
  selectedChat,
  setMessages,
  user,
}) => {
  const fullName = `${directContacts["participants"][0]?.first_name} ${directContacts["participants"][0]?.last_name}`;
  const shortName = `${directContacts["participants"][0]?.first_name?.charAt(
    0
  )} ${directContacts["participants"][0]?.last_name?.charAt(0)}`;

  const isOnline =
    directContacts["participants"][0]?.status &&
    directContacts["participants"][0]?.status === STATUS_TYPES.ACTIVE;
  const unRead = directContacts?.meta && directContacts?.meta?.unRead;

  let selectChat = selectedChat?._id ? selectedChat?._id : null;
  const isSelectedChat = directContacts?._id === selectChat ? true : false;
  const onClick = () => {
    setMessages("");
    onSelectChat(directContacts);
    updateLastConversationId(token, {
      user_id: directContacts?._id,
    });
  };

  return (
    <li className={classnames({ active: isSelectedChat })} onClick={onClick}>
      <Link to="#" className={classnames({ "unread-msg-user": unRead })}>
        <div className="d-flex align-items-center">
          <div
            className={classnames(
              "chat-user-img",
              "align-self-center",
              "me-2",
              "ms-0",
              { online: isOnline }
            )}
          >
            {directContacts["participants"][0]?.profile ? (
              <>
                <img
                  src={`${config.BACKENDURL}/profile/${directContacts["participants"][0]?.profile}`}
                  className="rounded-circle avatar-xs"
                  alt=""
                />
                {isOnline && <span className="user-status"></span>}
              </>
            ) : (
              <div className="avatar-xs">
                <span
                  className={classnames(
                    "avatar-title",
                    "rounded-circle",
                    "text-uppercase",
                    "text-white"
                  )}
                >
                  <span className="username"> {shortName}</span>
                  <span className="user-status"></span>
                </span>
              </div>
            )}
          </div>
          <div className="overflow-hidden">
            <p className="text-truncate mb-0">{fullName}</p>
          </div>
          {unRead && unRead !== 0 ? (
            <div className="ms-auto">
              <span className="badge badge-soft-dark rounded p-1">
                {unRead}
              </span>
            </div>
          ) : null}
        </div>
      </Link>
    </li>
  );
};

export default ChatUser;
