import React, { useEffect, useRef, useCallback, useState } from "react";

// hooks
import { useRedux } from "hooks/index";

// components
import AppSimpleBar from "components/AppSimpleBar";
// import Loader from "components/Loader";
import Message from "./Message";
// import Day from "./Day";

import ForwardModal from "components/ForwardModal";

// actions
import { forwardMessage, deleteImage } from "redux/actions";
import MessageLoader from "components/MessageLoader";

const Conversation = ({
  chatUserDetails,
  onDelete,
  onSetReplyData,
  isChannel,
  allMessages,
  selectedChat,
  user,
  loading,
  newMessages,
  setAllMessages,
}) => {
  // global store
  const { dispatch } = useRedux();

  // const { getUserConversationsLoading, isMessageForwarded } = useAppSelector(
  //   (state) => ({
  //     getUserConversationsLoading: state.Chats.getUserConversationsLoading,
  //     isMessageForwarded: state.Chats.isMessageForwarded,
  //   })
  // );

  const ref = useRef();
  const scrollElement = useCallback(() => {
    if (ref && ref.current) {
      const listEle = document.getElementById("chat-conversation-list");
      let offsetHeight = 0;
      if (listEle) {
        offsetHeight = listEle.scrollHeight - window.innerHeight + 250;
      }
      if (offsetHeight) {
        ref.current
          .getScrollElement()
          .scrollTo({ top: offsetHeight, behavior: "smooth" });
      }
    }
  }, [ref]);

  useEffect(() => {
    if (ref && ref.current) {
      ref.current.recalculate();
    }
  }, []);
  useEffect(() => {
    if (allMessages) {
      scrollElement();
    }
  }, [allMessages, scrollElement]);

  /*
  forward message
  */
  const [forwardData, setForwardData] = useState();
  const [isOpenForward, setIsOpenForward] = useState(false);

  const onOpenForward = (message) => {
    setForwardData(message);
    setIsOpenForward(true);
  };

  const onCloseForward = () => {
    setIsOpenForward(false);
  };

  const onForwardMessage = (data) => {
    const params = {
      contacts: data.contacts,
      message: data.message,
      forwardedMessage: forwardData,
    };
    dispatch(forwardMessage(params));
  };

  // useEffect(() => {
  //   if (isMessageForwarded) {
  //     setIsOpenForward(false);
  //   }
  // }, [isMessageForwarded]);

  /* image delete */
  const onDeleteImage = (messageId, imageId) => {
    dispatch(deleteImage(chatUserDetails.id, messageId, imageId));
  };

  useEffect(() => {
    if (newMessages) {
      setAllMessages([...allMessages, newMessages]);
    }
    // eslint-disable-next-line
  }, [newMessages]);
  return (
    <AppSimpleBar
      scrollRef={ref}
      className="chat-conversation p-3 p-lg-4 positin-relative"
    >
      {loading && <MessageLoader />}
      <ul
        className="list-unstyled chat-conversation-list"
        id="chat-conversation-list"
      >
        {(allMessages || []).map((message, key) => {
          const isFromMe =
            message?.receiverId === selectedChat["participants"][0]?._id + "";
          return (
            <Message
              message={message}
              key={key}
              selectedChat={selectedChat}
              onDelete={onDelete}
              onSetReplyData={onSetReplyData}
              isFromMe={isFromMe}
              onOpenForward={onOpenForward}
              isChannel={isChannel}
              onDeleteImage={onDeleteImage}
              user={user}
            />
          );
        })}
        {/*  <Day /> */}
      </ul>
      {isOpenForward && (
        <ForwardModal
          isOpen={isOpenForward}
          onClose={onCloseForward}
          forwardData={forwardData}
          chatUserDetails={chatUserDetails}
          onForward={onForwardMessage}
        />
      )}
    </AppSimpleBar>
  );
};

export default Conversation;
