import React from "react";
// import { Spinner } from "reactstrap";
import { ScatterBoxLoader } from "react-awesome-loaders";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { compose } from "redux";

const Loader = (props) => {
  const { user } = props;

  const themeColors = [
    { id: "bgcolor-radio1", color: "#4eac6d" },
    { id: "bgcolor-radio2", color: "#50a5f1d9" },
    { id: "bgcolor-radio3", color: "#6153ccd9" },
    { id: "bgcolor-radio4", color: "#e83e8cd9" },
    { id: "bgcolor-radio5", color: "#ef476fd9" },
    { id: "bgcolor-radio6", color: "#ffd166d9" },
    { id: "bgcolor-radio7", color: "#06d6a0d9" },
  ];

  const color = themeColors
    // eslint-disable-next-line
    .map((i) => {
      if (i.id === user?.themeColor) {
        return i.color;
      }
    })
    .find((n) => n);

  return (
    <div className="loader-container">
      <div className="status">
        <ScatterBoxLoader primaryColor={color} />
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.Navigation?.user,
  };
};

export default compose(withRouter, connect(mapStateToProps, null))(Loader);
